import React, { useState } from "react";
import { useSelector } from "react-redux";
const Table = () => {
  const selector = useSelector((state) => state.githubReducer);
  const { loading } = selector;
  return (
    <table className="table">
      <thead>
        <tr>
          <th>Title</th>
          <th>Description</th>
          <th>Language</th>
          <th>Creation Date</th>
        </tr>
      </thead>
      <tbody>
        {loading && (
          <tr>
            <td>Loading...</td>
            <td>Loading...</td>
            <td>Loading...</td>
            <td>Loading...</td>
          </tr>
        )}
      </tbody>
      {!loading &&
        selector.githubInfo.map((projects, index) => (
          <tbody>
            <tr>
              <td>{projects.name}</td>
              <td>
                {projects.description ? projects.description : "No Description"}
              </td>
              <td>Loading...</td>
              <td>Loading...</td>
            </tr>
          </tbody>
        ))}
    </table>
  );
};

export default Table;
