import {
  GITHUB_PROJECT_INFO_FAIL,
  GITHUB_PROJECT_INFO_REQ,
  GITHUB_PROJECT_INFO_SUCC,
  GITHUB_PROJECT_LANG_FAIL,
  GITHUB_PROJECT_LANG_REQ,
  GITHUB_PROJECT_LANG_SUCC,
} from "../Constants/githubConstants";
const initialState = { githubInfo: [] };
export const githubReducer = (state = initialState, action) => {
  switch (action.type) {
    case GITHUB_PROJECT_INFO_REQ:
      return { ...state, loading: true };
    case GITHUB_PROJECT_INFO_SUCC:
      return { loading: false, githubInfo: action.payload };
    case GITHUB_PROJECT_INFO_FAIL:
      return { ...state, loading: true };
    default:
      return { ...state, loading: true };
  }
};
export const languageReducer = (state = { lang: [] }, action) => {
  switch (action.type) {
    case GITHUB_PROJECT_LANG_REQ:
      return { ...state };
    case GITHUB_PROJECT_LANG_SUCC:
      return { loading: false, lang: action.payload };
    case GITHUB_PROJECT_LANG_FAIL:
      return { ...state };
    default:
      return { ...state };
  }
};
