import React, { useEffect, useState } from "react";
import Table from "../Components/Table";
import { useDispatch, useSelector } from "react-redux";
import { requestDatafromGithub, get_languages } from "../actions/GithubActions";
import { languageReducer, githubReducer } from "../reducers/githubReducer";
import { Link } from "react-router-dom";
const Projects = () => {
  const dispatch = useDispatch();
  const selector = useSelector((state) => state.githubReducer);
  const { githubInfo } = selector;
  const selectfromlangs = useSelector((state) => state.languageReducer);
  const [lang, setlang] = useState([]);
  useEffect(() => {
    //req data from github
    dispatch(requestDatafromGithub());

    //get lang for projects from github
    dispatch(get_languages());
    githubInfo.map((name, index) => {
      setlang(githubInfo[index].name);
      dispatch(get_languages(lang));
      console.log(lang[index]);
    });
  }, []);
  return (
    <section id="project" className="p-2">
      <div className="popular-projects">
        <h2>Popular Projects</h2>
        <Table />
      </div>
      <div className="future">
        <h2>Future Ventures(Teach myself in 2021)</h2>
        <div className="future-list">
          <li>Machine Learning/AI</li>
          <li>Data Science</li>
          <li>OAuth</li>
          <li>Nextjs/ElectronJs</li>
          <li>Making coffee instantly</li>
        </div>
        <h3>
          I have interests besides tech that i may share on my youtube channel
          and sometime live on twitch
        </h3>
      </div>
      <div className="contact-btn">
        <Link to="/contact">
          <button type="button">Contact Me</button>
        </Link>
        <button type="button">My Websites</button>
      </div>
    </section>
  );
};

export default Projects;
